import React from 'react'
import Logo from '../Assets/WelcomePageAssets/MixedAssets.svg'
import TextBG from '../Assets/WelcomePageAssets/TEXTBG.svg'
import ScrollDownSvg from '../Assets/WelcomePageAssets/scrollDown.svg'
import '../index.css'

const WelcomePage = () => {
    return (
        <div >
            <div 
                class="WelcomePage" 
                id="WelcomePageId"
                style={{
                    backgroundImage: `url(${TextBG})`,
                    backgroundPosition: `-200px 10px`,
                    backgroundSize: `1000px 250px`,
                    backgroundRepeat: `no-repeat`,

                }} 
            >
                <div class="leftLineContainer">
                    <div class="topLine"></div>
                    <div>
                        <i class="bi bi-facebook" style={{fontSize: `25px`, color:`rgb(199, 199, 199)`}}></i>    
                    </div>
                    <div>
                        <i class="bi bi-linkedin" style={{fontSize: `25px`, color:`rgb(199, 199, 199)`}}></i>
                    </div>
                    <div>
                        <i class="bi bi-envelope" style={{fontSize: `25px`, color:`rgb(199, 199, 199)`}}></i>
                    </div>     
                    <div class="bottomLine"></div>
                </div>
                <div class="container" id="WelcomeContainer">
                    <div class="row" id="ownRow">
                        <div class="col-1"></div>
                        <div class="col">
                            <h1 class="line1">WE TRANSFORM YOUR</h1>
                            <h1 class="line2">IDEAS TO REALITY</h1>
                            
                        </div>
                        <div class="col-4">
                            <img class="bulbLogo" src={Logo} alt="Logo" /> 
                        </div>
                        <div class="col-1"></div>
                        
                    </div>
                    
                </div>
                <div class="rightLineContainer">
                    <div class="rightTopLine"></div>
                    <div >
                        <p class="sideText">SCROLL DOWN</p>
                    </div>
                    <div >
                        <img class="ScrollDownSvg" src={ScrollDownSvg} alt="ScrollDownSvg" />
                    </div>
                </div>
                
            </div>
        </div>
    )
}

export default WelcomePage
