import React from 'react'
import '../index.css'
import Button from './Button'

function SystemsIntegration() {
    return (
        <div>
             <div class="SystemsIntegration" id="SystemsIntegrationID">
                {/* SystemsIntegration */}
                <div class="SystemsIntegration_container">
                    <div class="SystemsIntegration_text" >
                        <h1>
                            Principal capacities covered in our technology implementation support:
                        </h1>
                    </div>
                    <div class="SystemsIntegration_btn_container" >
                        <div style={{display: `flex`}}>
                            <div style={{marginRight:`100px`}}>
                                <Button Class="SystemsIntegration_btn" text="Service strategy and roadmap" />    
                            </div>
                            <div style={{marginTop: `-5px`}}>
                                <button class="SystemsIntegration_long_btn" type="button">Program delivery<br/> <p class="SystemsIntegration_long_btn_p">(program management, release management, benefits management)</p></button>
                            </div>
                        </div>
                        <div style={{display: `flex`}}>
                            <div style={{marginRight:`100px`}}>
                                <Button Class="SystemsIntegration_btn" text="Solution architecture and design" />
                            </div>
                            <div style={{marginRight:`100px`}}>
                                <Button Class="SystemsIntegration_btn" text="Requirements definition" />
                            </div>
                            <div style={{marginRight:`100px`}}>
                                <Button Class="SystemsIntegration_btn" text="Vendor package and vendor selection" />
                            </div>
                        </div>
                        
                        
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SystemsIntegration
